import { Component, Input, OnInit } from '@angular/core';
import { FormGroup } from "@angular/forms";

@Component({
  selector: 'lib-unisys-angular-input-wrapper',
  template: `<div class="form-group" [formGroup]="group" [ngClass]="customClass != '' && customClass">
    <label class="center-block"> <span translate>{{ translation}}</span>
        <ng-content></ng-content>
    </label>
    <ul *ngIf="fieldName">
        <li *ngFor="let item of field.errors | keyvalue">
            <span class="text-red" style="font-size: 11px;font-weight: 700;" translate>{{ item.key}}</span> <span *ngIf="isString(item.value)" translate> {{ item.value}}</span>
            <span *ngIf="!isString(item.value)">
          <ul>
            <li *ngFor="let subitem of item.value | keyvalue">
              <span translate>{{ subitem.key}}</span> : {{ subitem.value}}
            </li>
          </ul>
        </span>
        </li>
    </ul>
</div>`,
  styles: [`label>span{font-weight:500;font-size:12px}.form-control.ng-invalid{border-color:#dd4b39}.form-control.ng-valid{border-color:#c3dccd}ul{padding-left:0;list-style:none}ul li ul{padding-left:5px}.error{font-size:11px;font-weight:700}`]
})
export class UnisysAngularInputWrapperComponent implements OnInit {
  @Input('translation') translation;
  @Input('group') group: FormGroup;
  @Input('customClass') customClass: string = '';
  @Input('fieldName') fieldName: string = '';

  public field;

  constructor(){
  }

  ngOnInit(){
    this.fieldName != '' ? this.field = this.group.controls[this.fieldName] : this.field = null ;
  }

  public isString(val){
    return typeof val === 'string';
  }

}
